@import "../root.css";

#wtftext {
  margin-left: 0.5rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.pfp {
  width: 50px;
  height: 50px;
  float: left;
  border-radius: 50%;
  border: 4px solid var(--purple);
}

.pfp-post {
  vertical-align: text-bottom;
  width: 35px;
  height: 35px;
  float: left;
  border-radius: 50%;
  border: 2px solid var(--purple);
  margin-right: 3px;
}

.follow {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-top: 5px;
  margin-bottom: 5px;
}

.followbtn {
  border-radius: 10px;
  margin-left: 45px;
  cursor: pointer;
  height: 25px;
  background-color: var(--purple);
  border-style: none;
}